@import "./style/_vars.scss"; @import "./style/_mixins.scss";
.files {
  $this: &;

  &__item {
    display: flex;
    align-items: center;
    position: relative;
    margin-top: 15px;
    padding: 25px 0;

    &:after {
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 1px;
      background: $grey;
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
    }

    &:last-child {
      &:after {
        display: none;
      }
    }
  }

  &__aside {
    margin-left: auto;
  }
}